<template>
	<view class="check">
		
		<form @submit="formSubmit" @reset="formReset">
		<view class="check_list">
			<view class="list_box">
				<view class="title">所在国家</view>
				<view class="right">
					<view class="con">
						
						<picker @change="bindPickerChange" :value="index" :range="nationArray">
							<view class="uni-input">{{nationArray[index]}}</view>
						</picker>
					</view>
					<view class="icon xiangyou"></view>
				</view>
			</view>
			<view class="list_box">
				<view class="title">企业名称</view>
				<view class="right">
					<input type="text" placeholder="请输入企业名称"/>
					<view class="icon "></view>
				</view>
			</view>
			<view class="list_box">
				<view class="title">企业法人</view>
				<view class="right">
					<input type="text" value="" placeholder="请输入企业法人" />
					<view class="icon "></view>
				</view>
			</view>
			<view class="list_box">
				<view class="title">法人身份证号</view>
				<view class="right">
					<input type="text" value="" placeholder="请输入法人身份证号" />
					<view class="icon "></view>
				</view>
			</view>
			<view class="list_box">
				<view class="title">身份证有效期</view>
				<view class="right">
					<view class="con">
						<input type="text" value="" placeholder="格式为'YYYY-MM-DD'" />
						
					</view>
					<view class="icon "></view>
				</view>
			</view>
			<view class="list_box">
				<view class="title">营业执照编号</view>
				<view class="right">
					<input type="text" value="" placeholder="请输入营业执照编号" />
					<view class="icon "></view>
				</view>
			</view>
			<view class="list_box">
				<view>
					<view class="uploadText" @click="uploadImg(0)" v-if="!license" style="line-height: 68upx;">营业<br>执照</view>
					<view class="uploadImg" @click="uploadImg(0)" v-else>
						<image :src="license" mode="aspectFit"></image>
					</view> 
				</view>
				<view>
					<view class="uploadText" @click="uploadImg(1)"  v-if="!Assurface"><b style="font-size: 20upx;">法人身份证</b><br>人像面</view>
					<view class="uploadImg" @click="uploadImg(1)" v-else>
						<image :src="Assurface" mode="aspectFit"></image>
					</view> 
				</view>
				<view>
					<view class="uploadText" @click="uploadImg(2)"  v-if="!national"><b style="font-size: 20upx;">法人身份证</b><br>国徽面</view>
					<view class="uploadImg" @click="uploadImg(2)" v-else>
						<image :src="national" mode="aspectFit"></image>
					</view> 
				</view>
				<view>
					<view class="uploadText" @click="uploadImg(3)"  v-if="!handAssurface"><b style="font-size: 20upx;">手持证件照</b><br>人像面</view>
					<view class="uploadImg" @click="uploadImg(3)" v-else>
						<image :src="handAssurface" mode="aspectFit"></image>
					</view> 
				</view>
			</view>
			</view>
		</form>
	</view>
</template>

<script>
	import json from "../../../static/json/nation.json"
	export default{
		data(){
			
			return{
				headerPosition:"fixed",
				headerTop:null,
				statusTop:null,
				showHeader:true,
				index:"0",
				license:'',
				Assurface:'',
				national:'',
				handAssurface:'',
				nationArray:[],
				date:"",
				indate:'请选择'
			}
		},
		onLoad(){
			console.log(json)
			uni.getStorage({
				key:"userInfo",
				success:res=>{
					if(res.data.shopId){
						uni.redirectTo({
							url: '../shop'
						});
					}
				}
			})
			
			uni.getStorage({
				key:"lang",
				success:res=>{
					this.nationArray=json[res.data];
					// uni.setNavigationBarTitle({
					// 	title:json[res.data].title
					// })
				}
			});
			this.statusHeight = 0;
			// #ifdef APP-PLUS
			this.showHeader = false;
			this.statusHeight = plus.navigator.getStatusbarHeight();
			// #endif
		},
		
		methods:{
			exit(){
				uni.navigateBack({
					delta: 1
				});
			},
			uploadImg(e){
				console.log(e)
				uni.chooseImage({
					 sizeType: ['original', 'compressed'],
					 sourceType: ['album','camera'],
					 success: res=> {
						 if(e==0){
							this.license = res.tempFilePaths[0]
						 }
						 if(e==1){
						 	this.Assurface = res.tempFilePaths[0]
						 }
						 if(e==2){
						 	this.national = res.tempFilePaths[0]
						 }
						 if(e==3){
						 	this.handAssurface = res.tempFilePaths[0]
						 }
						 
					 }
				})
			}
		}
	}
</script>

<style lang="scss">
	page{
		background: #F4F4F4;
	}
	.status {
		width: 100%;
		height: 0;
		position: fixed;
		z-index: 10;
		background-color: #ffffff;
		top: 0;
		/*  #ifdef  APP-PLUS  */
		height: var(--status-bar-height);//覆盖样式
		/*  #endif  */
		
	}
	.header{
		width: 92%;
		padding: 0 4%;
		height: 100upx;
		display: flex;
		justify-content: space-between;
		align-items: center;
		position: fixed;
		color: #fff;
		top: 0;
		z-index: 10;
		background-color: #f06c7a;
		/*  #ifdef  APP-PLUS  */
		top: var(--status-bar-height);
		/*  #endif  */
	}
	.header button{
		margin: 10upx 20upx;
	}
	.place{
		background-color: #ffffff;
		height: 40upx;
		/*  #ifdef  APP-PLUS  */
		margin-top: var(--status-bar-height);
		/*  #endif  */
	}
	input{text-align: right;font-size: 30upx;}
	.check_list{width: 100%;background: #ffffff;}
	.list_box+.list_box{border-top: 1px solid #cecece;}
	.list_box{
		display: flex;
		padding: 20upx 0;
		justify-content: space-between;
		align-items: center;
		line-height: 80upx;
		width: 96%;
		margin: auto;
		background: #ffffff;
		.title{
			color: #999;
			font-size: 30upx;
		}
		.right{
			display: flex;
			align-items: center;
			color:#333;
		}
		.uploadText{
			width: 150upx;
			height: auto;
			border-radius: 5px;
			font-size: 38upx;
			text-align: center;
			color: #999;
			border: 4upx dashed  #999;
			padding: 5upx;
			// align-items: center;
			// display: flex;
			// justify-content: center;
		}
		.uploadImg{
			width: 150upx;
			height: 150upx;
			margin: 10upx;
			image{
				width: 100%;
				height: 100%;
			}
		}
	}
	.icon{
		width: 40upx;
		font-size: 35upx;
		color: #cecece;
		margin-left: 10upx;
	}
</style>
